@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.3.0/font/bootstrap-icons.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #121b22;
}
:root {
  --bg: #121b22;
  --secondaryBg: #1f2c34;
  --toastBg: #6d6c6c71;
  --green: #00a884;
}

body {
  background-color: (--bg);  
}

input {
  outline: 0;
  border-width: 0 0 0.1rem;
  background-color: rgba(0, 0, 0, 0);
  color: white;
  font-size: larger;
}

button {
  background-color: #00a884;
  background-color: var(--green);
  color: white;
  outline: none;
  border: none;
  cursor: pointer;
  font-size: larger;
  padding: 2%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  transition: background-color 0.5s;
}

button:disabled {
  opacity: 0.5;
}

input:focus {
  border-color: #00a884;
  border-color: var(--green)
}

i {
  color: white;
  font-size: 5vw;
}

.App {
  display: grid;
  grid-template-areas:
  'header'
  'body'
}

.app_header {
  background-color: #121b22;
  background-color: var(--bg);
}

.glass {
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 99;
}

.clickable {
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

span.ripple {
  position: absolute; /* The absolute position we mentioned earlier */
  border-radius: 50%;
  transform: scale(0);
  animation: ripple 600ms linear;
  background-color: rgba(255, 255, 255, 0.7);
}

@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}


#login-form{
    display: grid;
}

#login-logo {
    width: 60%;
    padding-bottom: 20%;
}

#login-form {
    width: 80%;
    display: grid;
    grid-row-gap: 30%;
    row-gap: 30%;
    padding-bottom: 40%;
}

#login-root {
    height: 100vh;
    display: grid;
    align-items: center;
    justify-items: center;
    align-content: center;
}

.login-spinner {
    width: 100%;
    display: grid;
    justify-content: center;
}
.spinner-bg-circle {
    border: 3px solid rgba(128, 128, 128, 0.397);
    border-top: 3px solid var(--green);
    border-right: 3px solid var(--green);
    border-bottom: 3px solid var(--green);
    width: 15px;
    height: 15px;
    border-radius: 50%;
    animation: spinner 1s ease-in-out infinite;
    transition: opacity 0.5s;
    opacity: 1;
}

.spinner-show {
 opacity: 1;
}

.spinner-hide {
 opacity: 0;
}

@keyframes spinner {
  0% {transform: rotate(0deg);}
  100% {transform: rotate(360deg);}
}

.toast {
  position: fixed;
  background-color: var(--toastBg);
  color: white;
  padding: 2% 5% 2% 5%;
  border-radius: 1000px;
  max-width: 80%;
  min-width: 30%;
  text-align: center;
  bottom: 10vh;
  transition: bottom .5s ease-in-out, opacity .5s ease-in-out;
  white-space: pre-wrap;
  overflow: hidden;
}

.toast-show {
  bottom: 10vh;
  opacity: 1;
}

.toast-hide {
  bottom: -20vh;
  opacity: 0;
}

.mainMenu {
    position: fixed;
    z-index: 100;
    left: 0;
    width: 70%;
    height: 100vh;
    background-color: var(--secondaryBg);
    transition: left .3s ease-in-out, opacity .3s ease-in-out;
    box-shadow: #0005 2px 2px 10px;
}

.mainMenuItem {
    color: white;
    padding: 3vw;
}

.mainMenu#show {
    left: 0px
}

.mainMenu#hide {
    left: -80vw;
}

.mainMenuProfile {
    color: white;
    display: grid;
    justify-items: center;
}

.mainMenuProfileAvatar {
    display: grid;
    background: cadetblue;
    font-size: xxx-large;
    font-weight: 400;
    border-radius: 50%;
    width: 10vw;
    height: 10vw;
    align-content: center;
    justify-content: center;
    margin: 3vw;
}

.mainMenuProfileName {
    font-size: larger;
    font-weight: 400;
}



.header_logo {
    width:12vw;
}

.app_header {
    position: relative;
    display: flex;
    height: 6vh;
    width: 100%;
    background-color: var(--secondaryBg);
    align-items: center;
    justify-content: space-between;
}

#menuButton {
    margin: 0% 3% 0% 0%;
    font-size: 4vw;
}

#slideMenuButton {
    margin: 0% 0% 0% 3%;
}

.clickableIcon {
    display: flex;
    width: 6vw;
    height: 6vw;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    border-radius: 50%;
}
.pageMenu {
    position: fixed;
    z-index: 100;
    right: 0;
    width: 50%;
    background-color: var(--secondaryBg);
    transform-origin: 100% 0%;
    transition: transform .3s ease-in-out, opacity .3s ease-in-out;
    box-shadow: #0005 2px 2px 10px;
}

.pageMenuItem {
    color: white;
    padding: 3vw;
}

.pageMenu#show {
    transform: scale(1);
    opacity: 1;
}

.pageMenu#hide {
    transform: scale(0);
    opacity: 0;
}



.attendenceSeacrhHolder {
    display: grid;
    justify-items: center;
}

.attendenceSearchInput {
    width: 70%;
    margin-top: 5vh;
}
